@font-face {
  font-family: "BeaufortforLOL-Bold.woff";
  font-style: normal;
  font-weight: normal;
  src: url("../assets/BeaufortforLOL-Bold.woff.ttf") format("woff", "OneType", "TrueType");
}

h2 {
  font-family: "BeaufortforLOL-Bold.woff";
}

div.logoChampion,
div.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}


.user{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  width: 10rem;
  height: 8rem;
  /* background-color: brown; */
}

/* div.info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  width: 10rem;
  height: 8rem;
} */

.frameIcon{
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-position: center;
  background-repeat: repeat;
  background-size: cover; */
  /* background-color: aquamarine; */
  width: 10vw;
  min-height: 15vh;
}

div.avatar {
  border-radius: 100%;
  width:5rem;
  min-height: 5rem;
  background-color: coral;
  background-position: center;
  background-size: cover;
  box-shadow: 2px 0px 14px 9px rgba(0,0,0,0.29);
}

div.rank {
  background-size: cover;
  background-position: center;
  width: 5rem;
  height: 5rem;
  margin-bottom: 8%;
  /* background-color: cornflowerblue; */
}

div.tag {
  background-size: cover;
  background-position: center;
  width: 6rem;
  height: 6rem;
  /* background-color: rgb(237, 100, 225); */
}

.userHero, .backgroundUpload {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 90vh;
  /* background-color: crimson; */
}

/* .backgroundUpload {
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
} */

.userside {
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly; */
  align-items: center;
  height: 80vh;
  /* background-color: white; */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  align-self: flex-start;
  margin-bottom: -20%;
}

.user h3, .info h4{
  font-family: 'Secular One', sans-serif;
  letter-spacing: 0,5px;
  text-transform:capitalize;
  font-size: 5.5vmin;
  color:black;
  font-style: italic;
  text-shadow: 3px 2px 6px rgb(0 0 0 / 33%);
}

.info h4{
font-size: 5vmin;
text-transform: uppercase;
}

.mainBackHome {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 110vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.championback {
  display: flex;
  justify-content: center;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  /* min-height: 80vh; */
  /* background-color: tomato; */
    padding: 1rem;
    border-width: 3px;
    border-style: solid;
    border-image: 
        linear-gradient(
        rgba(224, 191, 122,0), 
        rgba(224, 191, 122), 
        rgba(149, 118, 62, 0)
        ) 1 100%;
}

.champion {
    width: 100%;
    height: 65vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center top;
    position: relative;
    z-index: 1;
    /* background-color: violet; */

}

.championBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  min-height: 80vh;
  align-self: flex-start;
  /* margin-top: 3%; */
}

.dividerTopProfile, .dividerButtomProfile{
  width: 60%;
  height: 6vh;
  background-repeat: no-repeat;
  background-size: contain;
}

.dividerTopProfile{
  margin-bottom: -2%;
}

.dividerButtomProfile{
  margin-top: -1%;
}

.usernameProfile, .rankNameProfile {
  font-family: 'Secular One', sans-serif;
  letter-spacing: 0,5px;
  text-transform:uppercase;
  font-size: 2.5vmin;
  color: rgb(189, 151, 81);
  /* font-style: italic; */
  text-shadow: 3px 2px 6px rgb(0 0 0 / 33%);
  text-align: center;
  line-height: 1;
}

.loaderdiv{
  background-color: black;
  width: 100%;
  min-height: 100vh;
  display:flex;
  justify-content: center;
  align-items: center;
}


.boxVideos {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.boxRefresh{
  width: 30%;
  height: 10vh;
  display: flex;
  /* flex-direction: column; */
  justify-content: space-around;
  align-items: center;
  align-self: center;
  margin-right: 20%;
}

.boxReport{
  width: 50%;
  height: 10vh;
  display: flex;
  /* flex-direction: column; */
  justify-content: flex-start;
  align-items: center;
  align-self: center;
}

.boxRefresh button {
  background-color: transparent;
  border: none;
  padding: 20%;
}

.refresh, .reportUser{
  width: 30px;
  height: 30px;
  color: rgb(189, 151, 81);
}

.refresh{
   transition: 0.9s ease-in-out;
   width: 35px;
}

.refresh:hover{
  transform: rotate(180deg)
}

.userSideBox{
  /* width:15%; */
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reportInput{
    flex: 1 1;
    background-color: transparent;
    border-color: rgb(175, 140, 23);
    border-radius: 5px;
    color: whitesmoke;
}

@media(max-width:550px){
  .championBox{
    display:none;
  }

  .frameIcon{
    width: 20vw;
  }
  div.rank {
    width: 7rem;
    height: 7rem;
  }

  .usernameProfile, .rankNameProfile {
    font-size: 3vmin;
  }

  .rankNameProfile{
    padding-top: 2%;
  }

  div.videoForm{
    width: 80%;
  }

  
}

@media (max-width:800px){
  .videoUser{
    width: 600px !important;
    height: 450px !important;
  }
}

@media (max-width:670px){
  .videoUser{
    width: 400px !important;
    height: 350px !important;
  }
}