@font-face {
  font-family: "BeaufortforLOL-Bold.woff";
  src: url("../assets/BeaufortforLOL-Bold.woff.ttf") format("woff", "OneType", "TrueType");
}

h2 {
  font-family: "BeaufortforLOL-Bold.woff";
}

.community{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}


.mainBackHome {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 110vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.textCommunity{
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 30%;
  height: 40vh;
  align-self: center;
  margin-bottom: -3%;
}

.heroCommunity{
    width: 100%;
    background-image: url("https://i.postimg.cc/PfvXvMLh/KDA-Evelynn.png");
    min-height: 100vh;
    background-attachment: fixed;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}


.photos {
    display: flex;
    flex-wrap: wrap;
    justify-content:center;
    align-items: center;
    width: 100%;
}

.photo {
    min-height: 17rem;
    width: 17rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin:1rem;
    border-radius: 2em;
}


.backgroundCommunity {
  display: flex;
    justify-content: center;
    width: 100%;
    min-height: 90vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-bottom: 4%;
    padding-top: 6%;
}

.picCommunity {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  width: 30%;
  min-height: 70vh;
  margin: 2rem;
  box-shadow: 10px 10px 21px -1px rgba(255,0,181,0.43), -12px -12px 21px -1px rgba(0,144,255,0.43), -1px -1px 21px 8px rgba(115,0,255,1);

}

.picBox {
  width: 100%;
  display: flex;
  justify-content: center;
}



@media(max-width:1200px){
  .textCommunity{
      width: 40%;
  }
}

@media (max-width: 990px){
  .picBox{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .picCommunity{
    width: 85%;
    height: 90vh;
  }
}

@media(max-width:780px){
  .textCommunity{
      width: 45%;
  }
}

@media(max-width:550px){
  .textCommunity{
      width:55%;
  }
}

